import Vue from 'vue';
import App from './App';
import VueRouter from 'vue-router';
import configRouter from './routers';
import layer from './libs/layer/layer.js'; // alert confirm message loading tips dialog iframe
import commonApi from './libs/commonApi';
Vue.mixin(commonApi);
layer.config({
	path: './src/libs/layer/' //layer
});

Vue.use(VueRouter);
const router = new VueRouter();

window.JST = window.JST || {}; //
JST.HOST = 'http://integral.api.justtong.com';  //
JST.SUCCESS = 10000;    //
JST.PERPAGE = 20;      //
JST.ISLOGIN = 0;

//
router.beforeEach((transition) => {
     if (transition.to.auth) {
        if (sessionStorage.NUT_MEDIA_USER) {
            transition.next();
        } else {
            transition.redirect('/login');
        }
    } else{  transition.next();}
});
//
router.afterEach((transition) => {
	 window.scrollTo(0,0);
});

configRouter(router);
router.start(Vue.extend(App), '#app');



