<style lang="sass" scoped>
.panel-body label {
    font-size: 14px;
    margin-left: 10px;
}

.panel-body>.form-horizontal>.form-group .nav>li>a {
    padding: 2px 10px;
}

.panel-body>.nav>li>a {
    height: 40px;
}

.line-height-14 {
    line-height: 14px;
}

.line-height-30 {
    line-height: 30px;
}

.padding-top-7 {
    padding-top: 7px;
}

.padding-top-9 {
    padding-top: 9px;
}
</style>
<template lang="html">
    <!--  -->
    <header class="template-header">
        <span>{{title}}</span>
        <!--  -->
        <button class="btn btn-default margin-left-10" v-if="edit" @click.prevent="back()"><i class="fa fa-reply"></i> </button>
    </header>
    <!--  -->
    <panel title="">
        <div slot="panel-body" class="panel-body">
            <ul class="nav nav-pills" v-if="!edit">
                <li :class="step == 1? 'active':''"><a class="line-height-14" @click="stepSwitch(1)"><br>()</a></li>
                <li :class="step == 6? 'active':''"><a class="line-height-14" @click="stepSwitch(6)"><br>()</a></li>
                <li :class="step == 5? 'active':''"><a class="line-height-14" @click="stepSwitch(5)"><br>()</a></li>
                <li :class="step == 2? 'active':''"><a class="line-height-30" @click="stepSwitch(2)"></a></li>
            </ul>
            <ul class="nav nav-pills" v-else>
                <li class="active" v-if="step == 1"><a class="line-height-14"><br>()</a></li>
                <li class="active" v-if="step == 6"><a class="line-height-14"><br>()</a></li>
                <li class="active" v-if="step == 5"><a class="line-height-14"><br>()</a></li>
                <li class="active" v-if="step == 2"><a class="line-height-30"></a></li>
            </ul>
        </div>
    </panel>
    <div class="tab-content">
        <!--  -->
        <panel title="">
            <div slot="panel-body" class="panel-body">
                <form class="form-horizontal" role="form">
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input class="form-control" type="text" placeholder="" v-model="productDetails.name">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input class="form-control" type="text" placeholder="" v-model="productDetails.name_show">
                        </div>
                    </div>
                    <div class="form-group" v-if="step == 6">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input type="text" class="form-control" placeholder="" v-model="productDetails.ticket_id">
                        </div>
                    </div>
                    <div class="form-group" v-if="step == 6 || step == 2">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input type="text" class="form-control" placeholder="" v-model="productDetails.stocks">
                        </div>
                    </div>
                    <div class="form-group" v-if="step == 5">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input class="form-control" type="text" placeholder="http://www.taobao.com/coupon?bagId=123&from=nuthd" v-model="productDetails.url">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-5">
                            <input class="form-control" type="text" placeholder="" v-model="productDetails.price">
                        </div>
                        <div class="col-sm-1">
                            <select class="form-control" v-model="productDetails.price_unit">
                                <option value=""></option>
                                <option value=""></option>
                            </select>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-8">
                            <!-- editor -->
                            <textarea id="goodsDetails" placeholder="" autofocus></textarea>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-8 simditor-sm">
                            <!-- editorUse -->
                            <textarea id="goodsUse" placeholder="" autofocus></textarea>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2" style="padding-top: 3px;">
                            <span></span>
                        </label>
                        <div class="col-sm-8">
                            <!--  -->
                            <ul class="nav nav-pills">
                                <li class="active" style="margin-left: 0;"><a href="#tab-img1" data-toggle="tab"></a></li>
                                <li><a href="#tab-img2" data-toggle="tab"></a></li>
                                <li><a href="#tab-img3" data-toggle="tab"></a></li>
                            </ul>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="tab-content">
                            <!--  -->
                            <div class="tab-pane active" id="tab-img1">
                                <div class="col-sm-8 col-sm-offset-2">
                                    <p class="text-red content-img">750 x 400JPG1MPSD</p>
                                </div>
                                <div class="col-sm-8 col-sm-offset-2">
                                    <!--  -->
                                    <upload-img :img-src.sync="productDetails.pic_banner" file-id="file1" upload-id="upload-1"></upload-img>
                                </div>
                            </div>
                            <div class="tab-pane" id="tab-img2">
                                <div class="col-sm-8 col-sm-offset-2">
                                    <p class="text-red content-img">300 x 200JPG40KPSD</p>
                                </div>
                                <div class="col-sm-8 col-sm-offset-2">
                                    <!--  -->
                                    <upload-img :img-src.sync="productDetails.pic_thumb" file-id="file2" upload-id="upload-2"></upload-img>
                                </div>
                            </div>
                            <div class="tab-pane" id="tab-img3">
                                <div class="col-sm-8 col-sm-offset-2">
                                    <p class="text-red content-img">100 x 70JPG40KPSDf8f8f8</p>
                                </div>
                                <div class="col-sm-8 col-sm-offset-2">
                                    <!--  -->
                                    <upload-img :img-src.sync="productDetails.pic_icon" file-id="file3" upload-id="upload-3"></upload-img>
                                </div>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </panel>
        <panel title="">
            <div slot="panel-body" class="panel-body">
                <form class="form-horizontal" role="form">
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input class="form-control" type="text" placeholder="" v-model="productDetails.integral">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input class="form-control" type="text" placeholder="0" v-model="productDetails.limit">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input class="form-control" type="text" placeholder="0" v-model="productDetails.day_limit">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6 padding-top-9">
                            <switch-input :callback="limit" :exchange-limit="exchangeLimit"></switch-input>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input class="form-control" type="text" placeholder="0" disabled="{{!exchangeLimit}}" v-model="productDetails.day_all_limit">
                            <p class="text-red margin-top-10"></p>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-5 padding-left-0">
                            <div class="col-sm-5">
                                <input class="form-control" type="date" disabled="{{!exchangeLimit}}" v-model="productDetails.start_date">
                            </div>
                            <div class="col-sm-1 text-center padding-top-7">
                                <span></span>
                            </div>
                            <div class="col-sm-5">
                                <input class="form-control" type="date" disabled="{{!exchangeLimit}}" v-model="productDetails.end_date">
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-5 padding-left-0">
                            <div class="col-sm-5">
                                <input class="form-control" type="time" disabled="{{!exchangeLimit}}" v-model="productDetails.start_time">
                            </div>
                            <div class="col-sm-1 text-center padding-top-7">
                                <span></span>
                            </div>
                            <div class="col-sm-5">
                                <input class="form-control" type="time" disabled="{{!exchangeLimit}}" v-model="productDetails.end_time">
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </panel>
        <panel title="/" v-if="step == 2">
            <div slot="panel-body" class="panel-body">
                <form class="form-horizontal">
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-9">
                            <label class="radio-inline col-lg-1 col-sm-2">
                                <input type="radio" name="inlineRadioOptions" id="radio1" value=1 v-model="productDetails.send_type">
                            </label>
                            <label class="radio-inline col-lg-1 col-sm-2">
                                <input type="radio" name="inlineRadioOptions" id="radio2" value=2 v-model="productDetails.send_type">
                            </label>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-6">
                            <input type="text" class="form-control" placeholder="" v-model="productDetails.take_address" :disabled="productDetails.send_type == 1">
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">
                            <span></span>
                        </label>
                        <div class="col-sm-2">
                            <input type="text" class="form-control" placeholder="" v-model="productDetails.over_time" :disabled="productDetails.send_type == 1">
                        </div>
                        <div class="col-sm-1">
                            <select class="form-control" :disabled="productDetails.send_type == 1">
                                <option></option>
                            </select>
                        </div>
                    </div>
                </form>
            </div>
        </panel>
    </div>
    <footer>
        <!--  -->
        <button id="btn" class="btn button-primary" v-if="!edit">{{step == 1? '' : ''}}</button>
        <!--  -->
        <button class="btn button-primary" v-if="edit" @click.prevent="editProduct()"></button>
        <button class="btn button-primary margin-left-5" @click.prevent="preview()"></button>
    </footer>
    <phone-modal :show.sync="show">
        <div slot="modal-body">
            <product-preview :product-detail="productDetails"></product-preview>
        </div>
    </phone-modal>
</template>
<script>
import panel from '../../components/panel'; //panel
import uploadImg from '../../components/upload-img'; //
import switchInput from '../../components/switch-input'; //switch
import phoneModal from '../../components/phone-modal';
import productPreview from '../../components/preview/product_preview';
export default {
    components: {
        panel,
        uploadImg,
        switchInput,
        phoneModal,
        productPreview
    },
    data() {
        return {
            productDetails: {}, //
            editor: {}, //editor
            editorUse: {}, //editorUse
            step: '',
            title: '', //
            productId: '', //ID
            show: false,
            edit: false, //edit modal
            exchangeLimit: false, //switchfalse
            edited: false //true
        }
    },
    route: {
        data(transition) {
            //
            this.clearCondition();
            if (transition.to.query.id) {
                this.title = "";
                this.edit = true;
                this.edited = true;
                this.productId = transition.to.query.id;
            } else {
                this.title = "";
                this.edit = false;
                this.productId = '';
                this.step = 1;
                //editedtrue
                this.edited ? this.editor.setValue('') && this.editorUse.setValue('') : '';
            }
        }
    },
    ready() {
        var btn = document.getElementById('btn');
        btn.addEventListener('click', this.debounce(this.insertProduct, 300))
        this.initEditor(); //
        this.getProduct(); //
    },
    methods: {
        //
        initEditor() {
            this.editor = new Simditor({
                textarea: $('#goodsDetails'),
                placeholder: '',
                params: {},
                upload: true,
                tabIndent: true,
                toolbar: [
                    'title', 'bold', 'italic', 'underline', 'strikethrough', 'fontScale', 'color', 'ol', 'ul', 'blockquote', 'code', 'table', 'link', 'image', 'hr', 'alignment'
                ],
                toolbarFloat: true,
                toolbarFloatOffset: 0,
                pasteImage: true,
                upload: {
                    url: JST.HOST + '/mproduct/uploadimg',
                    params: null,
                    fileKey: 'upload_file',
                    connectionCount: 3
                }
            });
            this.editorUse = new Simditor({
                textarea: $('#goodsUse'),
                placeholder: '',
                params: {},
                upload: true,
                tabIndent: true,
                toolbar: [
                    'title', 'bold', 'italic', 'underline', 'strikethrough', 'fontScale', 'color', 'ol', 'ul', 'blockquote', 'code', 'table', 'link', 'image', 'hr', 'alignment'
                ],
                toolbarFloat: true,
                toolbarFloatOffset: 0,
                pasteImage: true,
                upload: {
                    url: JST.HOST + '/mproduct/uploadimg',
                    params: null,
                    fileKey: 'upload_file',
                    connectionCount: 3,
                    leaveConfirm: ''
                }
            })
        },
        //
        insertProduct() {
            let param = this.productDetails;
            param.type = this.step;
            param.content = this.resizeImg(this.resizeFontSize(this.editor.getValue()));
            param.content_use = this.resizeImg(this.resizeFontSize(this.editorUse.getValue()));
            this.requestApi("/mproduct/insert", param, (response) => {
                let info = this.step == 1 ? "" : "";
                layer.msg(info, {
                    time: 1000
                }, () => {
                    this.$route.router.go({
                        name: "goodsList"
                    });
                });
            })
        },
        //
        editProduct() {
            let param = this.productDetails;
            param.type = this.step;
            param.content = this.resizeImg(this.resizeFontSize(this.editor.getValue()));
            param.content_use = this.resizeImg(this.resizeFontSize(this.editorUse.getValue()));
            this.requestApi("/mproduct/update/" + this.productId, param, (response) => {
                layer.msg("", {
                    time: 1000
                }, () => {
                    this.$route.router.go({
                        name: "goodsList"
                    });
                });
            })
        },
        //
        getProduct() {
            //50px
            $(".simditor-toolbar").css("top", "50px");
            // 
            $(".simditor-body").css("overflow-x", "auto");
            if (this.productId) {
                this.requestApi("/mproduct/get/" + this.productId, {}, (response) => {
                    this.step = response.data.type;
                    this.productDetails = response.data;
                    this.editor.setValue(this.productDetails.content);
                    this.editorUse.setValue(this.productDetails.content_use);
                    this.productDetails.start_date = response.data.start_date? response.data.start_date.split(' ')[0] : '';
                    this.productDetails.end_date = response.data.end_date? response.data.end_date.split(' ')[0] : '';
                    let data = this.productDetails;
                    this.exchangeLimit = (data.day_all_limit || data.start_date || data.end_date || data.start_time || data.end_time) ? true : false;
                })
            } else {
                this.step = 1;
            }
        },
        //
        clearCondition() {
            this.productDetails = {
                name: '',
                type: '',
                price: '',
                price_unit: '',
                integral: '',
                content: '',
                content_use: '',
                status: 1,
                name_show: '',
                url: '',
                stocks: '',
                ticket_id: '',
                send_type: 1,
                open_flag: 2,
                take_address: '',
                over_time: ''
            };
            this.exchangeLimit = false;
        },
        //switch
        limit() {
            this.exchangeLimit = !this.exchangeLimit;
        },
        //
        stepSwitch(step) {
            this.step = step;
            window.scrollTo(0, 0);
        },
        preview() {
            console.log(this.editor.getValue());
            console.log(this.editorUse.getValue());
            this.productDetails.content = this.resizeImg(this.resizeFontSize(this.editor.getValue()));
            this.productDetails.content_use = this.resizeImg(this.resizeFontSize(this.editorUse.getValue()));
            this.show = true;
        },
        debounce(callback, delay) {
            var timer = null;
            return function() {
                clearTimeout(timer);
                timer = setTimeout(function() {
                    callback();
                }, delay)
            }
        }
    }
}
</script>



